import React from 'react';
import logo from '../assets/img/footer_03.png';

export default class Footer extends React.Component {
  render() {
    return (
            <div class="footer">
                <div class="container">
                    <div class="row">
                        <div class="col-md-6">
                            <div class="foot-logo">
                                <img src={logo} alt="" />
                                <span>Digital Service and Analytics Limited</span>
                            </div>
                            <div class="foot-nav">
                                <ul>
                                    <li><a href="/">Home</a></li>
                                    <li><a href="/">About Us</a></li>
                                    <li><a href="/">Feature</a></li>
                                    <li><a href="/">Service</a></li>
                                    <li><a href="/">Pricing</a></li>
                                </ul>
                            </div>
                            <p>A wonderful serenity has taken possession 
                                of my entire soul, like these sweet mornings 
                                of spring which I enjoy with my whole heart. 
                                I am alone, and feel the charm of existence in 
                                this spot, which was created for the bliss of 
                                souls like mine.</p>
                        </div>
                        <div class="col-md-6">
                            <div class="wrapaddress">
                                <div class="address">
                                    <h5>Head Quarter:</h5>
                                    <span>32/A3, Jashimuddin Avenue, Sector 3, Uttara, Dhaka 1230, Bangladesh.</span>
                                    <span>Contact Number: <a href="tel:+880-1730070600">+880-1730070600</a></span>
                                </div>
                                <div class="address">
                                    <h5>Brunch Office:</h5>
                                    <span>Zenith Corporate Park, G-77-02, Kelana Jaya SS7/26, Petaling Jaya, 47301, Selangor, Malaysia.</span>
                                    <span>Tel: 03-7887 5501 Fax:03-7887 5504</span>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-md-12">
                            <p>2018 Nitto Digital and Analytics Limited || All rights reserved </p>
                        </div>
                    </div>
                </div>
            </div>
    );
  }
}



