import React from 'react';
import { Button, Form, FormGroup, Label, Input, } from 'reactstrap';

export default class ContactForm extends React.Component {
  render() {
    return (
        <div class="Contact-Us">
            <div class="container">
                <div class="row">
                    <div class="col-md-12">
                        <div class="contact-wrap">
                            <h1>CONTACT</h1>
                            <Form class="" action="index.html" method="post">
                                <div class="row">
                                    <div class="col-md-6">
                                        <FormGroup>
                                          <Label for="exampleName">Your Name</Label>
                                          <Input type="text" name="name" id="exampleName" placeholder="Full Name" />
                                        </FormGroup>
                                    </div>
                                    <div class="col-md-6">
                                        <FormGroup>
                                          <Label for="exampleEmail">Your Email</Label>
                                          <Input type="email" name="email" id="exampleEmail" placeholder="Email" />
                                        </FormGroup>
                                    </div>
                                    <div class="col-md-12">
                                        <FormGroup>
                                          <Label for="exampleName">Subject</Label>
                                          <Input type="text" name="name" id="exampleName" placeholder="Subject" />
                                        </FormGroup>
                                    </div>
                                    <div class="col-md-12">
                                        <FormGroup>
                                          <Label for="exampleText">Your message</Label>
                                          <Input type="textarea" name="text" id="exampleText" />
                                        </FormGroup>
                                    </div>
                                    <div class="col-md-12">
                                        <FormGroup>
                                          <Button>Send</Button>
                                        </FormGroup>
                                    </div>
                                </div>
                            </Form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
  }
}



